.card {
    position: relative;
}
.card img{
    border: 2px solid #fff;
    border-radius: 6px;
    width:100%;
    display: block;
}

.card .front {
    position: absolute;
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    transition-delay: 0s;
}

.flipped .front{
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

.card .back{
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;

}
.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}
